import React, { useState } from 'react';
import { Input, InputNumber } from 'antd';
import './CodemaskConfig.css';

export interface mask {
  start?: number;
  end?: number;
  mask?: string;
}

interface Tprops extends mask {
  onChange?: (mask: mask) => void;
}

const Codemask: React.FC<Tprops> = props => {
  const { start = 0, end = 0, mask = '*', onChange } = props;
  const [codemask, setCodemask] = useState<Tprops>({
    start: start,
    end: end,
    mask: mask,
  });

  const inChange = (value: any, type: 'start' | 'end' | 'mask') => {
    codemask[type] = value;
    setCodemask(codemask);
    if (onChange) onChange(codemask);
  };

  return (
    <>
      <div className="select_row">
        <div className="select_label">掩码起始位</div>
        <div className="select_value codemask_config">
          <InputNumber
            min={1}
            max={10}
            defaultValue={start}
            onChange={v => {
              inChange(v, 'start');
            }}
          />
        </div>
      </div>
      <div className="select_row">
        <div className="select_label">掩码结束位</div>
        <div className="select_value codemask_config">
          <InputNumber
            min={1}
            max={10}
            defaultValue={end}
            onChange={v => {
              inChange(v, 'end');
            }}
          />
        </div>
      </div>
      <div className="select_row">
        <div className="select_label">掩码显示符</div>
        <div className="select_value codemask_config">
          <Input
            defaultValue={mask}
            onChange={v => {
              inChange(v.target.value, 'mask');
            }}
          />
        </div>
      </div>
    </>
  );
};

export default Codemask;
