/* eslint-disable */
import * as freemarker from 'freemarker-parser';
import { NodeType } from './index';

export const parseTemplate = (obj: any, str = ''): string => {
  if (!obj.object && !obj.property) return str;
  if (obj.object) {
    str += parseTemplate(obj.object, str);
  }
  if (obj.object.name) {
    str += obj.object.name;
  }
  if (obj.property.name) {
    str += `.${obj.property.name}`;
  }
  return str;
};

export const parseFunc = (data: any): string => {
  // ${mask(white.phone,2,3,'*')}
  // ${convertShow(user.$mp_sex,'{"男":"先生","女":"女士","未知","用户"}')}
  // ${numberFormat(item.price,'{"scale":3,"roundingMode":"ROUND_HALF_DOWN","unit":"个"}')}
  const { callee } = data;
  const func = callee.name;
  let q = '';
  let args: string[] = [];
  data.arguments.forEach((arg: any) => {
    if (arg.type === 'MemberExpression') {
      q += parseTemplate(arg);
    } else if (arg.type === 'Literal') {
      args.push(arg.raw);
    }
  });
  let params = args.join(',');
  return `\${${func}(${q},${params})}`;
};

/* 终极解析 */
export const parseRawValueX = (str: string) => {
  const parser = new freemarker.Parser();
  const parsed = parser.parse(str);
  const nodes: NodeType[] = [];
  if (parsed && parsed.tokens.length) {
    const matches = parsed.tokens;
    matches.forEach(token => {
      if (token.type === 'Text') {
        nodes.push({
          object: 'text',
          text: token.text,
          marks: [],
        });
      } else if (token.type === 'Interpolation') {
        let choosedProp = `${token.startTag}${token.params}${token.endTag}`;
        nodes.push({
          object: 'inline',
          type: 'USERPROPS',
          data: { choosedProp },
          nodes: [
            {
              object: 'text',
              text: '',
              marks: [],
            },
          ],
        });
      }
    });
  }
  // 解决在 Modal 中编辑时直接点击插入部分无法插入的老问题
  nodes.unshift({
    object: 'text',
    text: '',
    marks: [],
  });
  const obj = {
    object: 'value',
    document: {
      object: 'document',
      data: {},
      nodes: [
        {
          object: 'block',
          type: 'paragraph',
          data: {},
          nodes,
        },
      ],
    },
  };
  return obj;
};
