import React, { FC } from 'react';
import { Modal, Tabs } from 'antd';
import BcmProps from '../bcm-props';

const { TabPane } = Tabs;

// api 路径
export type URLS = {
  event2Url: string;
  product2Url: string;
};

type keys = 'user' | 'event' | 'product' | 'white';
export interface IValue {
  type: keys;
  firstOpt?: string;
  secondOpt?: string;
  preBuilt?: string;
  call?: string;
  arguments?: any[];
}

interface IProps {
  activeKey: keys;
  onTab: (v: string) => void;
  initialValue: IValue;
  visible: boolean;
  onConfirm: (v: string) => void;
  onCancel: () => void;
  userList: any[];
  eventList: any[];
  productList: any[];
  whiteList: any[];
  getEventProperty: (v: string) => Promise<void>;
  getProductProperty: (v: string) => Promise<void>;
  getWhiteProperty: (v: string) => Promise<any>;
}

const InserModal: FC<IProps> = ({
  activeKey = 'user',
  onTab = () => {},
  visible,
  onConfirm,
  onCancel,
  userList = [],
  eventList = [],
  productList = [],
  whiteList = [],
  getEventProperty,
  getProductProperty,
  getWhiteProperty,
  initialValue,
}) => {
  const { type, ...value } = initialValue || { type: 'user' };
  return (
    <Modal
      width="560px"
      title="插入动态参数"
      visible={visible}
      footer={null}
      mask={false}
      onCancel={onCancel}
      destroyOnClose
    >
      <Tabs activeKey={activeKey} onChange={onTab}>
        <TabPane tab="用户信息" key="user">
          <BcmProps
            ptype="user"
            // initialValue="${user.$mp_wx6228eb2adfba3ab4_qr_scene_str}"
            // initialValue={{
            //   firstOpt: '$mp_wx6228eb2adfba3ab4_qr_scene_str',
            //   call: 'mask',
            //   arguments: [2, 3, '*'],
            // }}
            // ${numberFormat(item.price,'{"scale":3,"roundingMode":"ROUND_HALF_DOWN","unit":"个"}')}
            // initialValue={{
            //   firstOpt: 'acceptable_maximum_loss',
            //   call: 'numberFormat',
            //   arguments: [`'{"scale":3,"roundingMode":"ROUND_HALF_DOWN","unit":"个"}'`],
            // }}
            // ${convertShow(user.$mp_sex,'{"男":"先生","女":"女士","未知","用户"}')}
            initialValue={type === 'user' ? value : null}
            readonly={false}
            onCancel={onCancel}
            onConfirm={onConfirm}
            userList={userList}
            eventList={eventList}
            productList={productList}
            whiteList={whiteList}
            getEventProperty={getEventProperty}
            getProductProperty={getProductProperty}
            getWhiteProperty={getWhiteProperty}
          />
        </TabPane>
        <TabPane tab="事件信息" key="event">
          <BcmProps
            ptype="event"
            initialValue={type === 'event' ? value : null}
            readonly={false}
            onCancel={onCancel}
            onConfirm={onConfirm}
            userList={userList}
            eventList={eventList}
            productList={productList}
            whiteList={whiteList}
            getEventProperty={getEventProperty}
            getProductProperty={getProductProperty}
            getWhiteProperty={getWhiteProperty}
          />
        </TabPane>
        <TabPane tab="产品信息" key="product">
          <BcmProps
            ptype="product"
            initialValue={type === 'product' ? value : null}
            readonly={false}
            onCancel={onCancel}
            onConfirm={onConfirm}
            userList={userList}
            eventList={eventList}
            productList={productList}
            whiteList={whiteList}
            getEventProperty={getEventProperty}
            getProductProperty={getProductProperty}
            getWhiteProperty={getWhiteProperty}
          />
        </TabPane>
        <TabPane tab="白名单信息" key="white">
          <BcmProps
            ptype="white"
            initialValue={type === 'white' ? value : null}
            readonly={false}
            onCancel={onCancel}
            onConfirm={onConfirm}
            userList={userList}
            eventList={eventList}
            productList={productList}
            whiteList={whiteList}
            getEventProperty={getEventProperty}
            getProductProperty={getProductProperty}
            getWhiteProperty={getWhiteProperty}
          />
        </TabPane>
      </Tabs>
    </Modal>
  );
};

export default InserModal;
