import React, { useState } from 'react';
import { Select, Switch, Radio, InputNumber } from 'antd';
const { Option } = Select;
const { Group } = Radio;
import './NumberConfig.css';

export interface digital {
  unit?: string;
  fixType?: string;
  decimalPos?: number;
  round?: boolean;
}
interface TProps extends digital {
  onChange?: (data: digital) => void;
}

const units: string[] = [
  '个',
  '十',
  '百',
  '千',
  '万',
  '十万',
  '百万',
  '千万',
  '亿',
  '十亿',
  '百亿',
];

const NumberConfig: React.FC<TProps> = (props) => {
  const {
    unit = '个',
    fixType = 'keepInt',
    decimalPos = 2,
    round = false,
    onChange,
  } = props;
  const [digitalConfig, setDigitalConfig] = useState<digital>({
    unit: unit,
    fixType: fixType,
    round: round,
    decimalPos: decimalPos,
  });

  const inChange = (
    value: any,
    type: 'unit' | 'fixType' | 'round' | 'decimalPos',
  ) => {
    const data = { ...digitalConfig, [type]: value };
    setDigitalConfig(data);
    if (onChange) {
      onChange(data);
    }
  };

  return (
    <>
      <div className="select_row">
        <div className="select_label">取值数量级</div>
        <div className="select_value number_config">
          <Select
            onChange={(value: string) => {
              inChange(value, 'unit');
            }}
            defaultValue={unit}
            getPopupContainer={() => document.body}
          >
            {units.map((item, index) => (
              <Option key={index} value={item}>
                {item}
              </Option>
            ))}
          </Select>
        </div>
      </div>
      <div className="select_row">
        <div className="select_label">取整方式</div>
        <div className="select_value number_config">
          <Group
            onChange={(e) => {
              inChange(e.target.value, 'fixType');
            }}
            value={fixType}
          >
            <Radio value={'keepInt'}>取整</Radio>
            <Radio value={'keepFloat'}>
              <span className="select_value_label">保留小数位</span>
            </Radio>
          </Group>
          {fixType === 'keepFloat' && (
            <InputNumber
              min={1}
              max={10}
              defaultValue={decimalPos}
              onChange={(v) => {
                inChange(v, 'decimalPos');
              }}
            />
          )}
        </div>
      </div>
      <div className="select_row">
        <div className="select_label">四舍五入</div>
        <div className="select_value number_config">
          <Switch
            defaultChecked={round}
            onChange={(checked) => {
              inChange(checked, 'round');
            }}
          />
        </div>
      </div>
    </>
  );
};

export default NumberConfig;
