import React, { useState, useRef, useEffect, FC } from 'react';
import { Input } from 'antd';

import './PropertyMap.css';

interface IProps {
  data: any[];
  onChange: (val: any[]) => void;
}

const PropertyMap: FC<IProps> = ({ onChange, data }) => {
  const [forms, setForms] = useState(data);
  const index = useRef(data.length + 1);

  const onAdd = () => {
    const list = forms.slice();
    list.push({ key: '', val: '', index: index.current++ });
    setForms(list);
  };

  const onDel = (index: number) => {
    const list = forms.slice();
    const i = list.findIndex(f => f.index === index);
    list.splice(i, 1);
    setForms(list);
  };

  const onInputChange = (key: 'key' | 'val', index: number, val: string) => {
    const list = forms.slice();
    const i = forms.findIndex(f => f.index === index);
    if (i !== -1) {
      list[i][key] = val;
      setForms(list);
    }
  };

  const onInputBlur = () => {
    // const data = forms.filter(item => item.key !== '' && item.val !== '');
    onChange && onChange(forms);
  };

  useEffect(() => {
    // 表单改变 触发事件
    onInputBlur();
  }, [forms.length]);

  return (
    <div className="promap-container">
      <div className="promap-title">属性显示转换</div>
      <div className="promap-box">
        <div className="promap-filed">
          <div className="promap-filed-header">
            <div className="promap-filed-title">实际值</div>
            <div className="promap-filed-title">显示值</div>
          </div>
          {forms.map((form, index) => (
            <div className="promap-filed-item" key={form.index}>
              <Input
                placeholder="请输入"
                value={form.key}
                onChange={ev =>
                  onInputChange('key', form.index, ev.target.value)
                }
                onBlur={onInputBlur}
                style={{ width: 218, marginRight: 18 }}
              />
              <Input
                placeholder="请输入"
                value={form.val}
                onChange={ev =>
                  onInputChange('val', form.index, ev.target.value)
                }
                onBlur={onInputBlur}
                style={{ width: 218 }}
              />
              {index > 0 && (
                <span
                  className="promap-filed-del"
                  key={form.index}
                  onClick={() => onDel(form.index)}
                >
                  ×
                </span>
              )}
            </div>
          ))}
        </div>
        <div className="promap-add">
          <span onClick={onAdd}>
            <span className="promap-add-icon">+</span>转换配置
          </span>
        </div>
      </div>
    </div>
  );
};

export default PropertyMap;
